body {
  margin-right: auto;
  margin-left: auto;
  padding: 0;
  text-align: center;  
  background-color: white; 
} 

.App {
  width: 100%;
  height: 100%;
}

iframe {
  border: none;
  width: 80%;
  height: 1000px;;
}

a {
  font-size: 22px;
  font-family: 'Special Elite';
  color: #253031;
  text-decoration: none;
  -webkit-transition: .1s;
  -o-transition: .1s;
  transition: .1s;
  line-height: 40%;
}

h1 {
  font-size: 22px;
  font-family: 'Special Elite';
  color: #253031;
  line-height: 40%;
}

h2 {  
  font-family: 'Oswald', sans-serif;
  color: #449c9e;
  line-height: 40%;
} 

h3 {  
  font-family: 'Special Elite';
  color: #253031;
  line-height: 40%;
} 

a:hover {
  color: #bec5ad;  
}

p {
  font-family: 'Merriweather', sans-serif;
  color: #253031;
  font-size: 18px;
}

@media screen and (min-width: 560px) {
  body {
    width: 560px;
  }
}

@media screen and (min-width: 767px) {
  body {
    width: 767px;
  }
}

@media screen and (min-width: 1200px) {
  body {
    width: 1200px;
  }
}



